.organizar_semestres{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px 0;
}

.botao_grade_horaria{
    background-color: #FFACAC;
    width: 80%;
    border: none;
    border-radius: 10px;
    padding: 8px 20px;
    gap: 0 10px;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.materias{
    width: 80%;
    height: 70%;
}

.grade_ativa{
    display: flex;
    flex-direction: column-reverse;
    gap: 15px 0px;
}

@media screen and (min-width: 750px) {
    .botao_grade_horaria{
        display: none;
    }

    .materias{
        display: grid;
        grid-template-columns: 28% 70%;
        gap: 0 2%;
    }
}
